<template>

  <div class="wrapper">
    <div class="modal__box">
      <section class="loginSignup__container">
        <div class="loginSignupBox">
          <div class="loginSignupBox__heading">
            <!-- <img src="../../images/harulogo.svg" alt="" class="login__logo"> -->
            <h1 class="loginSignup__heading"></h1>
          </div>

        <form class="login__form" id="login__form"
              ref="form" method="POST" @submit.prevent="login" enctype="multipart/form-data">
          <fieldset class="login__filedset">
            <legend class="login__legend">  log in form </legend>
            <label class="login__label-email" for="user-id"></label>
            <input v-model="user_input.email" name="email" type="email" class="login__input-email" id="user-id" placeholder="" required>
            <label class="login__label-password" for="user-pw"></label>
            <input v-model="user_input.password" name="password" type="password" class="login__input-password" id="user-pw" placeholder="" minlength="8" required>
            <button class="login__button" type="submit"></button>
            <button class="login__button-facebook">
              <span class="fb__icon"></span>
              <span href="#" class="login__facebook">Facebook </span>

            </button>
            <a href="#" class="login__password-find"> ?</a>
          </fieldset>
        </form>

          <div class="newuser__box">
            <p class="newuser__text"> ?</p>
            <!-- <a href="#" class="newuser_signup" @click="gotoSignup"></a> -->
              <router-link to = "/signup" active-class="current-page" class="newuser_signup"></router-link>
          </div>
      </div>
      </section>
    </div>
  </div>

</template>

<script>

export default {
  data() {
    return {

      user_input: {
        email: '',
        password: ''

      },
      datalist: [],
      resource: {},
      form: ''

    }
  },
   methods: {
        login() {
          var _this = this

            console.log('this', this)
            console.log('_this', _this)
          var userData = new FormData(this.$refs.form);

          axios.post('/login/', userData)
          .then(function (response) {

            console.log(':',response);

            if ( response.status === 200 ) {
               alert(_this.user_input.email + '  ^^');


                // _this.hasToken = response.data.key
                _this.$store.token = response.data.key
                // console.log('_this.hasToken :' , _this.hasToken)

                // store     
                // this.$store.token = _this.hasToken

                console.log('this.$store.token : ', _this.$store.token)

               _this.$router.push({path: '/home'});

                // Cookie setting
                setCookie('HaruToken', _this.$store.token, 90);

                // location.href = "/login"
            } else {

               alert('    ');

            }
          })
          .catch(function (error) {
            console.log(':',error);

            alert('    ');

          });
          function setCookie(name, value, expireDays) {
            var exdate=new Date();
            exdate.setDate(exdate.getDate() + expireDays);
            var c_value=escape(value) + ((expireDays==null) ? "" : "; expires="+exdate.toUTCString());
            document.cookie =name + "=" + c_value;
          }
    }
 }
}

</script>
<style lang="sass" scoped>
@import "../Sass_login_signup/login.sass"
</style>



