<template>
  <div class="wrapper">
  <div class="modal__box">
    <section class="loginSignup__container">
      <div class="loginSignupBox">
        <div class="loginSignupBox__heading">
          <!-- <img src="../../images/harulogo.svg" alt="" class="login__logo"> -->
          <h1 class="loginSignup__heading"></h1>
        </div>

      <form class="signup__form" id="signup__form" ref="form" method="POST" @submit.prevent="submitData" enctype="multipart/form-data">
        <fieldset class="signup__filedset">
          <legend class="signup__legend">  Signup form </legend>
          <label class="signup__label-email" for="user-id"></label>
          <input v-model="user_input.email" name="email" type="email" class="signup__input-email" id="user-id" placeholder="" required>
          <label class="signup__label-password" for="user-pw"></label>
          <input v-model="user_input.password" name="password" type="password" class="signup__input-password" id="user-pw" placeholder="" minlength="8" required >
          <label class="signup__label-passwordConfirm" for="confirm-userPw"></label>
          <input @keyup="validatePassword" type="password" class="signup__input-passwordConfirm" id="confirm-userPw"  placeholder="" minlength="8" >
          <!-- <input  type="password" class="signup__input-passwordConfirm" id="confirm-userPw"  placeholder="" minlength="8" > -->
          <button class="signup__button" type="submit"></button>
          <button class="signup__button-facebook">
            <span class="fb__icon"></span>
            <span href="#" class="signup__facebook">Facebook </span>

          </button>
        </fieldset>
      </form>

        <div class="user__box">
          <p class="user__text"> ?</p>
          <!-- <a href="#" class="newuser_signup">  </a> -->
          <router-link tag="a" to = "/login" active-class="current-page" class="newuser_signup">  </router-link>
        </div>
    </div>
    </section>
  </div>
</div>
</template>

<script>

export default {

  data() {
    return {

      user_input: {
        email: '',
        password: '',
        passwordConfirm: '',
        validation: false
      },
      datalist: [],
      resource: {},
      form: ''
    }
  },
   methods: {
    //    
     validatePassword(){
       var password = document.getElementById("user-pw")
       var confirm_password = document.getElementById("confirm-userPw");
       if(password.value !== confirm_password.value) {
        confirm_password.setCustomValidity("  .")
        this.user_input.validation = false;
       } else {
         confirm_password.setCustomValidity('');
         this.user_input.validation = true;
       }
     },
    //  submitData() {
    //    console.log("input:",this.user_input)
    //    console.log(this.$http)
    //    this.$http.post('http://haru-eb.ap-northeast-2.elasticbeanstalk.com/signup/', this.user_input)
    //              .then(function(response) {
    //                console.log("res:",response);
    //              })
    //              .catch(function(error) {
    //                console.log("Err",error.message);
    //              })
     //
    //      },
         submitData() {
           var _this  = this
            // console.log(_this);
           var userData = new FormData(this.$refs.form);

           axios.post('/signup/', userData)
           .then(function (response) {
             console.log(':',response.status)
             if ( response.status === 201 ) {
                alert(_this.user_input.email + '  .');

                _this.$router.push({path: '/login'});

             } else {

                alert(_this.user_input.email + '  .');

             }

           })
           .catch(function (error) {

            //     value   <div className=""></div>
             alert(_this.user_input.email + '  .');
             var email = document.querySelector('#user-id')
            //  var user_pw = document.querySelector('#user-pw')
            //  var confirm_userPw = document.querySelector('#confirm-userPw')
             email.value = '';
            //  user_pw.value = '';
            //  confirm_userPw.value = '';


             console.log(':',error);
           });



     }

   }
}
</script>
<style lang="sass" scoped>
  @import "../Sass_login_signup/signup.sass"
</style>



